.header {
  padding-top: 1rem;
  padding-right: 0;
  padding-bottom: 1rem;
  padding-left: 0;
}
.header__inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  row-gap: 1em;
  column-gap: 1em;
}
@media (max-width: 1200px) {
  .header__inner {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    row-gap: 1em;
    column-gap: 1em;
  }
}
.header__block {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  flex-wrap: nowrap;
  row-gap: 1em;
  column-gap: 1em;
}
.header__block--logo {
  width: 535px;
  height: min-content;
}
@media (max-width: 1200px) {
  .header__block--logo {
    width: 100%;
    height: initial;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    flex-wrap: nowrap;
    row-gap: 1em;
    column-gap: 1em;
  }
}
.header__block--contacts {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  flex-wrap: nowrap;
  row-gap: 1em;
  column-gap: 1em;
}
.header__description {
  font-size: 1.4em;
  line-height: 1.4em;
  font-weight: 400;
}
.header__btn {
  background: url(../img/header/edit.png) 1rem center no-repeat #fff;
  padding-top: 1rem;
  padding-right: 1rem;
  padding-bottom: 1rem;
  padding-left: 5rem;
  border: 2px solid #55a831;
  border-radius: 1rem;
  font-size: 1.4em;
  line-height: 1.2em;
  font-weight: 700;
}
.header__logo {
  width: 142px;
  height: 42px;
}
.header__time {
  width: 30px;
  height: 30px;
}
.header__phone--number {
  font-size: 2.3em;
  line-height: 1.2em;
  font-weight: 700;
  color: black;
}

.text-blue {
  color: #0089cd;
}

.text-orange {
  color: #ffb903;
}

.text-green {
  color: #55a831;
}

.text-white {
  color: #ffffff;
}

.uppercase {
  text-transform: uppercase;
}

.center {
  text-align: center;
}

.section-title {
  font-size: 3em;
  line-height: 1.4em;
  font-weight: 700;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 2em;
  margin-left: 0;
  padding-top: 0;
  padding-right: 0;
  padding-bottom: 2em;
  padding-left: 0;
  position: relative;
  top: auto;
  right: auto;
  left: auto;
  bottom: auto;
  transform: none;
}
.section-title::after {
  content: "";
  position: absolute;
  top: auto;
  right: auto;
  left: 50%;
  bottom: 0;
  transform: none;
  transform: translateX(-50%);
  width: 17rem;
  height: 0.2em;
  background: url(../img/glazing/line.png) no-repeat;
}
.section-title.text-white::after {
  background: url(../img/guarantees/header_sub.png) no-repeat;
}

.wrapper {
  max-width: 1320px;
  padding: 0 10px;
  margin: 0 auto;
}

button:not(:disabled) {
  cursor: pointer;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #000000;
}

a {
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  opacity: 0.8;
  text-shadow: 0 0 10px #000000;
}

button:hover {
  opacity: 0.8;
  box-shadow: 0 0 10px #000000;
}

ul,
ol {
  list-style: none;
}

.glazing .aluminum,
.glazing .plastic,
.glazing .french,
.glazing .rise {
  display: none;
}

.decoration .external,
.decoration .rising,
.decoration .roof {
  display: none;
}

.faded {
  animation: faded 0.5s ease-in;
}

@keyframes faded {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
.feedback {
  background-color: #0089cd;
}
.feedback a {
  text-decoration: underline;
}

.main {
