.form {
  width: 504px;
  height: initial;
  margin-top: 2em;
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 0;
  padding-top: 2em;
  padding-right: 5em;
  padding-bottom: 2em;
  padding-left: 5em;
  border-radius: 15px;
  background-color: #ffffff;
  text-align: center;
}
