  margin-left: 0;
}
@media (max-width: 550px) {
  .form__title {
    font-size: 1.6em;
    line-height: 2em;
    font-weight: 700;
  }
}
.form__field {
  border-radius: 1rem;
  background-color: #ffffff;
  color: #000000;
  border: 1px solid #cecece;
  width: 100%;
  height: 5.8rem;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 2em;
  margin-left: 0;
  padding-top: 0;
  padding-right: 0;
  padding-bottom: 0;
  padding-left: 0;
  font-size: 1em;
  line-height: 1.5em;
  font-weight: 400;
}
.form__field::placeholder {
  font-size: 1.5em;
  line-height: 1.5em;
  font-weight: 400;
}
.form__notice {
  margin-top: 1em;
  margin-right: 0;
  margin-bottom: 2em;
  margin-left: 0;
  font-size: 1.1em;
