  font-weight: 700;
  color: #cecece;
}
.form__status {
  display: block;
  font-size: 1.4em;
  line-height: 1.4em;
  font-weight: 700;
}
.form__select {
  width: 100%;
  height: 5.8rem;
  font-size: 1.5em;
  line-height: 1.5em;
  font-weight: 400;
}
.form__icons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  row-gap: normal;
  column-gap: normal;
}
.form__icons img {
  width: 100px;
  height: initial;
}
.form__icon--active {
  border-top: 1px solid #0089cd;
}
.form__img img {
  width: 100%;
  height: initial;
}
.form .checkbox {
  display: none;
}
.form .checkbox-custom {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: auto;
  right: auto;
  left: auto;
  bottom: auto;
  transform: none;
  width: 22px;
  height: 22px;
  border: 2px solid #ccc;
  border-radius: 3px;
}
.form .checkbox-custom[id=warm]::before {
  content: "";
  background: url(../img/modal_calc/check_warm.png) center center no-repeat;
}
.form .checkbox-custom[id=cold]::before {
  background: url(../img/modal_calc/check.png) center center no-repeat;
}
.form .checkbox:checked + .checkbox-custom::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

