  line-height: 1.2em;
  font-weight: 700;
  color: black;
}

.dialog {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  transform: none;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.5);
}
.dialog__content {
  position: fixed;
  top: 10%;
  right: auto;
  left: 50%;
  bottom: auto;
  transform: none;
  transform: translateX(-50%);
}
.dialog__close {
  position: absolute;
  top: 20px;
  right: 0;
  left: auto;
  bottom: auto;
  transform: none;
  transform: translateX(-50%);
  font-size: 4em;
  line-height: 1em;
  font-weight: 700;
  color: #000000;
  border: none;
  background: 0 0;
  z-index: 10;
}
@media (max-width: 767px) {
  .dialog__gallery img {
    width: 100%;
  }
}